.line_behind_text{
	display: table;
	@media screen and (min-width: 768px) {
		white-space: nowrap;
	}
	&:before, &:after {
      border-top: 2px solid #00395e;
      content: '';
      display: table-cell;
      position: relative;
      top: 0.75em;
      width: 45%;
    }
	&--center {
		@extend .line_behind_text;
	    		
	}
	&--noleft {
		@extend .line_behind_text;
		&:before {
	      width: 0%;
	    }	
	    &:after {
	      width: 85%;
	    }
	}
    &:before { right: 1.5%; }
    &:after { left: 1.5%; }

}

