.bottom_line{
	&:before{
		position: absolute;
		content: "";
		left: 50%;
		bottom: -20px;
		transform: translateX(-50%);
		height: 1px;
		width: 94px;
		background: #545A71;
	}
}
.bottom_line1{
	&:before{
		position: absolute;
		content: "";
		left: 50%;
		bottom: -20px;
		transform: translateX(-50%);
		height: 1px;
		width: 94px;
		background: #CBB893;
	}
}
.bottom_line_2{
	&:before{
		position: absolute;
		content: "";
		left: 50%;
		bottom: -5px;
		transform: translateX(-50%);
		height: 1px;
		width: 100px;
		background: $primary_color;
	}
}
.how_we_work{
	border-top: 6px solid $primary_color;
	overflow: hidden;
	&:before{
		position: absolute;
	    content: "";
	    top: 33px;
	    right: -45px;
	    -webkit-transform: rotate(45deg);
	    transform: rotate(45deg);
	    height: 1px;
	    width: 94px;
	    background: #c8c8c8;
	}
	&:after{
		position: absolute;
	    content: "";
	    top: 33px;
	    right: -50px;
	    -webkit-transform: rotate(45deg);
	    transform: rotate(45deg);
	    height: 1px;
	    width: 180px;
	    background: #c8c8c8;
	}
	span{
		position: absolute;
	    top: 33px;
	    right: -33px;
	    -webkit-transform: rotate(45deg);
	    transform: rotate(45deg);
	    height: 1px;
	    width: 225px;
	    background: #c8c8c8;
	}
}
