.testimonials{
	background: url(../img/homepage-testimonials.png);
	background-size: 100% 100%;
	background-position: center;
	padding-top: 140px !important;
	position:relative;
	margin-top: 6rem;
	@media only screen and (max-width: 1024px) {
		margin-top: 4rem;
	}
	@media only screen and (max-width: 740px) {
		/*background: url(../img/testimonials-mobile.png);*/
		background-size: cover;
		padding-top: 100px !important;
		margin-top:0px;
	}
	.testimonial_icon{
		top: -10rem;
    	width: 41%;
    	left: -10rem;
	    @media only screen and (max-width: 1024px) {
	    	top: -1rem;
    		left: -5rem;	    }
	}
	&:before{
		content: "";
	    background: #fff;
	    width: 100%;
	    height: 13rem;
	    position: absolute;
	    top: -8rem;
	    left: 0;
	    z-index: 999;
	    transform: skewY(-4deg);
	    @media only screen and (max-width: 1024px) {
	    	transform: skewY(-8deg);
    		height: 13rem;
    		top: -4rem;
	    }
	    @media only screen and (max-width: 768px) {
		    transform: skewY(-13deg);
		    height: 180px;
		    top: -85px;
	    }
	    @media only screen and (max-width: 740px) {
	    	display:none;
	    }
	}
}
